export const MsgA = ({children}) => {
    return <div className="msg">{children}</div>;
}
export const MsgSuccessA = ({children}) => {
    return <div className="msg success">{children}</div>;
}
export const MsgErrorA = ({children}) => {
    return <div className="msg error">{children}</div>;
}
export const MsgHighlight = ({children}) => {
    return <div className="msg highlight">{children}</div>;
}